import { useContext } from "react";
import FormUtil from "../../../../utils/form/formUtiil";
import TreeUtil from "../../../../utils/treeUtil";
import FactoryTab, { FactoryTreeContext } from "../factoryTab";
import FactoryUtil from "../factoryUtil";

const FactDetFieldMng = () => {
    const { invalidate, node, setFocusNode } = useContext(FactoryTreeContext);
    const wrap = node.data as FactoryUtil.WrapNode;

    return (<>
        <FormUtil.BorderPanel
            title="operation"
            innerJsx={<FormUtil.ButtonRecord
                isCenter
                buttons={[
                    {
                        label: 'Add Field',
                        callback: () => {
                            // const newWrap: FactoryUtil.WrapNode = FactoryUtil.createParamWrap('field-item',
                            //     { name: '', formulaItems: ['var'], values: [{ type: 'direct', value: '0' }] }
                            // );
                            const newWrap: FactoryUtil.WrapNode = {
                                type: 'field-item',
                                data: null
                            };
                            (wrap.data as FactoryUtil.NodeFieldManager).fields.push(newWrap);
                            const newNode = TreeUtil.createNode(newWrap, node);
                            node.children.push(newNode);
                            setFocusNode(newNode);
                            invalidate();
                        }
                    }
                ]}
            />}
        />
    </>);
}

export default FactDetFieldMng;